import btnComponent from "@/components/btnComponent.vue"

export default {
	components: {
		btnComponent
	},
	data() {
		return {
			list: [],
			num: 1
		};
	},
	onShow() {
		this.loadData()
	},
	methods: {
		numChange(index, plus) {
			if (plus.detail) {
				this.list[index].num = plus.detail.value
			} else {
				if (plus) this.list[index].num++
				else this.list[index].num--
			}

			if (this.list[index].num < 1) this.list[index].num = 1
		},
		// 支付
		payment(item) {
			this.$app.request(this.$app.API.PAY_ORDER, {
				goods_id: item.id,
				goods_num: item.num,
				user_id: this.$app.getData('userInfo').id,
				type: 1,
			}, res => {
























				uni.requestPayment({
					provider: 'wxpay',
					timeStamp: res.data.timeStamp,
					nonceStr: res.data.nonceStr,
					package: res.data.package,
					signType: res.data.signType,
					paySign: res.data.paySign,
					success: res => {
						this.$app.toast('支付成功', 'success')
						this.loadData()
						this.$app.request(this.$app.API.USER_CURRENCY, {}, res => {
							this.$app.setData('userCurrency', res.data)
							this.userCurrency = this.$app.getData('userCurrency')
						})
					},
					fail: err => {
						this.$app.toast('支付失败')
					}
				});

			}, 'POST', true);

		},
		loadData() {
			this.$app.request('page/prop', {}, res => {

				for (let key in res.data) {
					res.data[key].num = 1
				}
				this.list = res.data
			})
		}
	},
}
